


document.addEventListener('DOMContentLoaded', () => {
    try {

        ScrollReveal().reveal('.ScrollReveal_right', { reset: false,  distance: '100%',
        origin: 'right', viewOffset: {
            top: 0,
            right: 0,
            bottom: -150,
            left: 0,
        }, })

        ScrollReveal().reveal('.ScrollReveal_left', { reset: false,  distance: '100%',
        origin: 'left' })


    } catch (e) {
        console.log(e);

    }

})